.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    position: relative;
    width: 80%;
    max-height: 80%;
    background-color: white;
    padding: 20px;
    overflow-y: auto; /* Enables vertical scrolling */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .modal-close-button {
    position: sticky;
    top: 0;
    float: right;
    background: none;
    border: none;
    color: black;
    font-size: 24px;
    cursor: pointer;
    z-index: 1001; /* Ensures the button stays above modal content */
    padding: 10px;
    margin-right: -10px; /* Adjusts the position slightly to align with the corner */
  }
  
  .modal-close-button:hover {
    color: red;
  }
  
  .modal-body {
    padding: 20px;
    max-height: 80vh;
    overflow-y: auto;
  }
  
  .modal-section {
    margin-bottom: 20px; /* Adds space between sections */
  }
  
  .gallery-container {
    column-count: 3;
    column-gap: 10px;
  }
  
  .gallery-item {
    width: 100%;
    margin-bottom: 10px;
    break-inside: avoid;
  }
  